html, body {
  height: 100%;
  margin: 0;
}

body {
  color: #d7d7ef;
  background-color: #37385f;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: PT Sans, sans-serif;
  display: flex;
}

h2 {
  text-align: center;
  margin: 1em 0;
}

.file-drop-area {
  width: 500px;
  max-width: 100%;
  box-sizing: border-box;
  border: 1px dashed #7c7db3;
  border-radius: 3px;
  margin: 0 auto;
  padding: 26px 20px 30px;
  transition: background-color .2s;
  position: relative;
}

.file-drop-area.is-active {
  background-color: #3f4069;
}

.fake-btn {
  text-transform: uppercase;
  background-color: #3f4069;
  border: 1px solid #9e9ec4;
  border-radius: 3px;
  margin-right: 8px;
  padding: 8px 15px;
  font-size: 12px;
}

.file-msg {
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: calc(100% - 130px);
  vertical-align: middle;
  font-size: small;
  font-weight: 300;
  display: inline-block;
  overflow: hidden;
}

.file-input {
  height: 100%;
  width: 100%;
  cursor: pointer;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.file-input:focus {
  outline: none;
}

.info {
  text-align: center;
  margin: 1em auto 33vh;
}

.info a {
  color: #d7efd7;
}

.info a:focus, .info a:hover {
  color: #d7efd7;
  text-decoration: none;
}

